import { useRef, useState } from "react";

const Video = () => {
	const videoEmbed = useRef();
	const [play, togglePlay] = useState(false);

	const playHandler = () => {
		if ( !play ) {
			!!videoEmbed.current && videoEmbed.current.play();
		} else {
			!!videoEmbed.current && videoEmbed.current.pause();
		}
		togglePlay(!play)
	}
	return(
		<div className={`video__player ${play ? 'playing' : ''}`}>
			<video ref={videoEmbed} poster={require("../../../assets/physical-marking-furniture.png")} width="100%" height="100%">
				<source src="https://unikbase-media.s3.fr-par.scw.cloud/Unikbase%20for%20Furniture%20Designers.mp4" type="video/mp4" />
			</video>
			<span onClick={playHandler} className="play-button"><span className="arrow"></span></span>
		</div>
	)
}

export default Video;