import React, { useEffect, useState } from "react";
import { 
  CssBaseline, 
  Grid,
  Typography,
  Container,
	Collapse,
} from "@mui/material";

import { getOfferId, getLang } from "../../helper/utils";
import { prices, offers } from "../../config/product";
import useInput from "../../hooks/use-Input";
import useHttp from "../../hooks/use-Http";
import PricingTableColumn from "./PricingTableColumn";

// import Link from '@mui/material/Link';
import { createTheme, ThemeProvider } from "@mui/material/styles";
import './LandingPage.scss';
import { useSearchParams } from "react-router-dom";
import { Box } from "@mui/material";

import FAQs from "./FAQ";
import Footer from "../../layout/Footer";
import Header from "../../layout/Header";
import Tracking from "../../components/Tracking/Tracking";
import UnikbaseAppIllustration from "./UnikbaseAppIllustration";
import Video from "./Video";

const REDIRECT_URL = process.env.REACT_APP_REDIRECT_URL || "https://unikbase.com";
const STRIPE_PAYMENT_URL = process.env.REACT_APP_STRIPE_PAYMENT_URL || "https://dev1.unikbase.dev/meveo/rest/strcheckout";


const theme = createTheme({
	breakpoints: {
		values: {
			lg: 1366
		}
	},
	typography: {
		fontFamily: [
			'Rubik'
		]
	},
	status: {
		danger: "#e53e3e",
	},
	palette: {
		primary: {
			main: "#EA5123",
			darker: "#053e85",
		},
		neutral: {
			main: "#64748B",
			contrastText: "#fff",
		},
	},
	invalid: {
		backgroundColor: "#fff",
		transition: "300ms",
		display: "flex",
		justifyContent: "center",
		flexDirection: "column",
		"& p": {
			color: "rgb(218, 63, 63)",
			marginTop: "0",
			transition: "400ms",
			fontSize: "0.5rem",
			textAlign: "center",
			marginRight: "3rem",
		},
	},
});

const LandingPageContent = ( props ) => {
	const [offer, setOffer] = useState({});
	const [status, setStatus] = useState('idle');


	const [searchParams] = useSearchParams();
	const urlParams = new URLSearchParams(window.location.search);

	let lang = urlParams.get("lang");
	if (!lang) lang = searchParams.get("lang");
	if (!lang) lang = navigator.language || navigator.userLanguage;

	const language = getLang(); //Replace it later by func
	const pageTexts = language.pages.landing_page;

	let pricingTables = pageTexts.pricing_table || {};

	//Converting tpk_id to get price from table
	let tpk_id;
	tpk_id = urlParams.get("tpk_id");
	if ( !tpk_id ) tpk_id = searchParams.get('tpk_id');
	let image = urlParams.get("image");
	if ( !image ) image = searchParams.get('image');


	const {
		isLoading: isLoadingPost,
		hasError: httpPostError,
		sendRequest: sendPostRequest,
		setIsLoading: setIsLoadingPost,
		setHttpError: setHttpErrorPost,
	} = useHttp({ url: STRIPE_PAYMENT_URL, method: "POST" });
	// const {isLoading:isLoadingGet,hasError:httpGetError,sendRequest:sendGetRequest,setIsLoading:setIsLoadingGet,setHttpError:setHttpErrorGet} = useHttp({url:'https://checkout.stripe.com',method: 'GET'})

	useEffect(() => {
		if ( !tpk_id ) {
			window.location.replace(REDIRECT_URL);
			return;
		}
		
		let offerId = getOfferId(tpk_id);
		let currentOffer = offers.find((item) => item.id === offerId)
		setOffer(currentOffer.columns);
	}, []);

	////

	const submitHandler = (data) => {

		let payload = {
			email: data.email,
			tpk_id,
			price_id: data.price_id,
			value: data.value, //price_item,
		};
		setStatus('loading');
		sendPostRequest(payload)
      .catch((error) => {
        setIsLoadingPost(false);
        setHttpErrorPost(error.message);
      })
      .then((url) => {
        // console.log("URL",data)
        if (url) {
          let session_id = url
            .split("https://checkout.stripe.com/c/pay/")
            .join("");
					session_id = session_id.split('#')[0]

          localStorage.setItem("session_id", session_id);
					localStorage.setItem(`_tpk_request_${session_id}`, JSON.stringify({
						...payload,
						image: image
					}));
          window.location.replace(url)
        }
      });
	};
	
	return (
  	<ThemeProvider theme={theme}>
			<CssBaseline />
			{!!(status === 'loading') && <div className="loading"></div>}
			<Header />
			<main>
				{!!tpk_id &&
					<Container 
						maxWidth="lg" 
						component="section"
						className="section bg--blue"
					>
						<Tracking order={tpk_id} />
						<div className="row">
							<div className="col col--1">
								
								<div id="product" className="row">
									<div className="col col--4-12 bg--white thumbnail">
										<img src={image} alt="Digital Asset" />
									</div>

									<div className="col col--8-12">
										<div className="product__detail bg--gray">
											<h3>Object Name</h3>

											<h4>Description</h4>
											<p>Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in hendrerit.</p>
											
											<h4>Price</h4> 
											<p><span></span> XXXXX.XX</p>

											<h4>Owners Email</h4>
											<p><a href="mailto:Email@mail.com" rel="noreferrer" target="_blank">Email@mail.com</a></p>
										</div>
									</div>
								</div>

								<div id="pricing-plans" className="row">
									<div className="col col--4-12 options">
										<h3>{pricingTables.options_title}</h3>
										<ul className="list">
											{pricingTables?.options.map((item, idx) => <li key={`pricin-plan-option-${idx}`}>{item}</li>)}
										</ul>
									</div>

									<div className="col col--8-12">
										<div className="row">
											{!!pricingTables.columns && pricingTables.columns.map((column, idx) => {
												let price = prices.find(el => el.id === offer[idx])
												return (
														<PricingTableColumn key={`offer-${idx}`} id={`column-${idx}`} price={price} image={image} column={column} callback={submitHandler} imageType={idx >= 1 ? '3D' : ''} />
												)
											})}
										</div>
									</div>
								</div>

							</div>
						</div>
					</Container>}

				<Container
					maxWidth="lg"
					component="section"
					className="section"
					id="about-digital-passport"
				>
					<div className="row">
						<div className="col col--1-3 bg--gray">
							<Video />
						</div>
						<div className="col bg--blue col--2-3">
							<Typography component="h3" variant="body1">What is a Digital Passport?</Typography>

							<Typography component="div" variant="body1">
								It is a unique digital secured file that contains any useful information available to The Packengers (description, dimensions, authenticity report, invoice, pictures, condition report...). You may update and use this passport to prove ownership, to certify authenticity, to report loss of theft, or simply to resell your item one day.
							</Typography>

							<Typography component="p" variant="body1">
								Your passport is now ready for you to download: create your account in a few clicks and access it via mobile app or on your computer.
							</Typography>
						</div>
					</div>
				</Container>

				<Container
					maxWidth="lg"
					component="section"
					className="section"
					id="benefits"
				>
					<div className="row">
						<div className="col col--2-3 bg--gray">
							<Typography component="h3" varaint="body1">Benefits</Typography>
							<ul className="list">
								<li className="item">
									<span className="item__title">Long-lasting, Reliable Storage</span>
									A secure Digital Passport rather than paper. The owner has control over the information, as well as a trusted third party if desired.
								</li>
								<li className="item">
									<span className="item__title">Enhanced Proof of Authenticity</span>
									Validate ownership of a genuine item with confidence. Our process includes sourcing and time-stamping information, with trusted third parties like artists, marketplaces, and experts record-
									ing documents in the passport. This linkage remains in place indefinitely, ensuring the authenticity of each unique object.
								</li>
								<li className="item">
									<span className="item__title">Access to Services</span>
									The passport serves as a medium for services such as assurance, as well as services offered by the double operator (invitations, offers, etc.).
								</li>
								<li className="item">
									<span className="item__title">Value and Exchange</span>
									When it's time to resell their property, the owner simply passes on their passport to the operator of their choice, who uses it to market the item.
								</li>
								<li className="item">
									<span className="item__title">Reassure</span>
									Re-establish confidence in a market for valuables today shaken by theft and counterfeiting, particularly on the Internet.
								</li>
								<li className="item">
									<span className="item__title">Access to Services</span>
									The passport serves as a medium for services such as assurance, as well as services offered by the double operator (invitations, offers, etc.).
								</li>
								<li className="item">
									<span className="item__title">Value and Exchange</span>
									When it's time to resell their property, the owner simply passes on their passport to the operator of their choice, who uses it to market the item.
								</li>
							</ul>
						</div>
						<div className="col col--1-3" style={{
							backgroundImage: `url(${require('../../assets/benefit-bg.jpg')})`,
							backgroundSize: 'cover',
							backgroundRepeat: 'no-repeat',
							backgroundPosition: 'top left'
						}}>
						</div>
					</div>
				</Container>
			</main>
			<Footer />
  	</ThemeProvider>
	);
}

export default LandingPageContent;
